import React from 'react'
import { recipes } from '../exampleData'
import RecipeContent from '../modules/recipes/RecipeContent'
import NotFound from '../pages/NotFound'

class Recipe extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            recipeContent: {},
            recipeExists: true
        }
    }
    componentDidMount() {
        const recipeContent = recipes.find((recipe) => { return recipe.id == this.props.match.params.id })
        if(recipeContent == null) {
            this.setState({recipeExists: true})
            this.props.history.push('/404')
        }
        else this.setState({recipeContent})
    }
    render() {
        return (
            <div className="recipe-page content-page">
                <div className="container">
                    <div className="row">
                        <div className="col-md-9">
                            <RecipeContent recipeContent={this.state.recipeContent} />
                        </div>
                        <div className="col-md-3">

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Recipe