import React from 'react'

class MealsSelector extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            customSelect: false,
            submitted: false
        };
        this.handleChange = this.handleChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    handleChange(e) {
        const selectValue = e.target.value;
        if(parseInt(selectValue) === 0) {
            e.target.disabled = true;
            this.setState(()=> ({
                customSelect: true
            }))
        }
    }
    onSubmit(e) {
        e.preventDefault();
        const mealForm = e.target.elements;
        const mealsCount = (this.state.customSelect) ? mealForm.mealsCountCustom.value : mealForm.mealsCountSelect.value;
        this.setState(()=> ({
            submitted: true
        }))
        this.props.onSubmit(mealsCount);
    }
    render() {
        return (
            <div className="meals-selector">
                <form onSubmit={this.onSubmit}>
                    <select className="meals-selector__select"  onChange={this.handleChange} name="mealsCountSelect">
                        <option value="2">ki</option>
                        <option value="3"></option>
                        <option value="5">Be</option>
                        <option value="0">zn Se</option>
                    </select>
                    {this.state.customSelect && <input className="meals-selector__custom" placeholder="Gnlk ne yemk sayn yazn" name="mealsCountCustom"/>}
                    <button className="action-button">{!this.state.submitted ? 'Yarat' : 'Yenidn Yarat'}</button>
                </form>
            </div>
        )
    }
}

export default MealsSelector