import { Link } from 'react-router-dom'

const Header = () => {
    return (
        <div className = "page-header-parent">
            <div className="page-header container">
                <div className="header-left">
                    <div className="header-logo">
                        <h1>pahriz</h1>
                    </div>
                    <div className="header-navigation">
                        <nav>
                            <ul>
                                <li>
                                    <Link to='/'>Diyeta Generator</Link>
                                </li>
                                <li>
                                    <Link to='/calculators'>Kalkulyatorlar</Link>
                                </li>
                                <li>
                                    <Link to='/foods'>Kalorilr</Link>
                                </li>
                                <li>
                                    <Link to='/articles'>Yazlar</Link>
                                </li>
                                <li>
                                    <Link to='/recipes'>Reseptlr</Link>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </div>
                <div className="header-right">
                    <div className="header-auth">
                        <div className="header-auth__buttons">
                            <Link className="login-button" to="">Giri</Link>
                            <Link className="register-button" to="">Qeydiyyat</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header