import React from 'react';
import FoodsList from '../modules/browseFoods/FoodsList'
import FoodsSearch from '../modules/browseFoods/FoodsSearch'
import FoodsFilter from '../modules/browseFoods/FoodsFilter'
import {applyCategoryAction, applySearchAction} from '../../actions/browseFoodsActions'
import { connect } from 'react-redux';

const Foods = (props) => {
    const applySearch = (searchValue) => {
        props.dispatch(applySearchAction(searchValue))
    }
    const applyCategory = (categories) => {
        props.dispatch(applyCategoryAction(categories));
    }
    return (
        <div className="browse-foods-page content-page">
            <div className="container">
                <div className="row">
                    <div className="col-md-8">
                        <FoodsSearch search={applySearch} />
                        <FoodsList foodsList={props.foods.browseFoods}/>
                    </div>
                    <div className="col-md-3 offset-md-1">
                        <FoodsFilter categories={props.foods.categories} applyCategory={applyCategory}/>
                    </div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state,props) => {
    return {
        foods: state.browseFoods
    }
}

export default connect(mapStateToProps)(Foods);